 @import url('https://fonts.googleapis.com/css2?family=Archivo+Black&display=swap');
 @import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300..700&display=swap');
 @import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&family=Quicksand:wght@300..700&display=swap');


$boldTitle: "Archivo Black", sans-serif;
$text: "Quicksand", sans-serif;
$title:  "Outfit", sans-serif; ;

$font: (
    'boldTitle': $boldTitle ,
    'text': $text,
     'title': $title,
);



$color : (
     'background': rgb(52, 50, 50),
     'backgroundLight': rgb(255, 255, 255),
     'primaryGreen': rgb(0, 96, 2) ,
     'secondaryGreen': rgb(86, 203, 92,0.6),
     'tertiaryGreen': rgb(0, 129, 43),
)